import React from "react";
import Hero from "../components/Hero";
import Content from "../components/Content";

function AboutPage(props) {
    return(
        <div>
            <Hero title={props.title}/>
            <Content>
                <p>Hello, my name is Daniel. I am a fourth year student at the University of Wisconsin Stout persuing a B.S. Computer Science. I am a Software Developer with experience with Java, C++, Python, C#, SQL Server, HTML, CSS and JavaScript.</p>
                
                <p>After I graduate, I aspire to become a fulltime front end or back end Software Engineer for a reputible company.</p>

                <p>I am always trying to learn new things. Currently these include web development using React, automation using Python and multithreading using C++/Java.</p>

                <p>Activities outside of programming I enjoy doing are playing video games, tennis, golf, bowling and weightlifting. However, the majority of my time is spent playing video games with my friends.</p>
            </Content>

        </div>
    );
}
 export default AboutPage;