import React from "react";
import Hero from "../components/Hero";
import Content from "../components/Content";

function ResumePage(props) {
    return(
        <div>
            <Hero title={props.title}/>
            <Content>
                <h5><b>Summary</b></h5>
                <p>4th year Computer Science student with 10 months CO-OP experience as a Software Developer looking for an internship/CO-OP</p>

                <h5><b>Work Experience</b></h5>
                <p><b>Software Systems Intern for Cardinal FG</b></p>
                <ul>
                    <li>Worked in a team of 3 to create and maintain projects running on the factory’s intraweb</li>
                    <li>Traveled across the country to help diagnose problems within the factories</li>
                    <li>Languages used include C#, ASP.NET, HTML, CSS, JavaScript and SQL Server</li>
                </ul>

                <p><b>Resident Advisor at University of Wisconsin Stout Housing</b></p>
                <ul>
                    <li>Supervise 30+ first year students through their transition to college</li>
                    <li>Solve problems in the building and between residents</li>
                    <li>Coordinate floor and building eventsto promote inclusivity</li>
                </ul>

                <p><b>Coursework</b></p>
                <ul>
                    <li>Established a strong foundation for object orientated programming</li>
                    <li>Learned basic web development and database manipulation</li>
                    <li>Implemented Agile processes in group projects</li>
                    <li>Maintained proper source control in a team environment</li>
                </ul>
            </Content>

        </div>
    );
}
 export default ResumePage;