import React from "react";
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import "./App.css";

import Footer from "./components/Footer";
import HomePage from "./pages/HomePage";
import AboutPage from "./pages/AboutPage";
import ResumePage from "./pages/ResumePage";
import ProjectsPage from "./pages/ProjectsPage";

class App extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            title: 'Daniel Staveness',
            headerLinks: [
                { title: 'Home', path: '/' },
                { title: 'About', path: '/about' },
                { title: 'Resume', path: '/resume' },
                { title: 'Projects', path: '/projects' }
            ],
            home: {
                title: 'Welcome!',
                subTitle: 'My Online Portfolio',
                text: 'Checkout the about me'
            },
            about: {
                title: 'About Me'
            },
            resume: {
                title: 'My Resume'
            },
            projects: {
                title: 'My Projects'
            }
        }
    }

    render() {
        return (
            <Router>
                <Container className="p-0 page" fluid={true}>

                    <Navbar className="border-bottom" bg="transparent" expand="sm">
                        <Navbar.Brand>Daniel Staveness</Navbar.Brand>

                        <Navbar.Toggle className="border-3" aria-controls="navbar-toggle" className="" />
                        <Navbar.Collapse id="navba-toggle">
                            <Nav className="ml-auto">
                                <Link className="nav-link" to="/">Home</Link>
                                <Link className="nav-link" to="/about">About</Link>
                                <Link className="nav-link" to="/resume">Resume</Link>
                                <Link className="nav-link" to="/projects">Projects</Link>

                            </Nav>
                        </Navbar.Collapse>
                    </Navbar>

                    <Route path="/" exact render={() => <HomePage title={this.state.home.title} subTitle={this.state.home.subTitle} text={this.state.home.text} />} />
                    <Route path="/about" exact render={() => <AboutPage title={this.state.about.title} />} />
                    <Route path="/projects" exact render={() => <ProjectsPage title={this.state.projects.title} />} />
                    <Route path="/resume" exact render={() => <ResumePage title={this.state.resume.title} />} />

                    
                </Container>
            </Router>
        );
    }
}

export default App;
